@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}
.navbar {
  padding: 20px;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #f1356d;
  text-align: center;
}

.content {
  max-width: 600px;
  margin: 5px auto;
  padding: 20px;
}

.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}

.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}

.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

.stockwatcher {
  width: 600px;
  height: 20px;
  overflow: hidden;
  display: inline;
}

.stockwatcher h1 {
  float: left;
  font-size: 20px;
  overflow: hidden;
}
.stocktracker {
  width: 600px;
  height: auto;
  margin-bottom: 10px;
}

.navbar p span {
  font-weight: bold;
}

.stockTicker {
  display: inline-block;
}

.stock-preview {
  display: inline;
}

.news-content {
  overflow: auto;
  max-height: 400px;
  border: 1px solid #fafafa;
}

.news-div {
  display: -webkit-flex;
  display: flex;
}

.news-source-image {
  max-height: 70px;
  max-width: 10%;
  float: left;
}

.news-headline {
  max-width: 40%;
  max-height: 70px;
  overflow: hidden;
  display: inline-block;
  white-space: nowrap;
  padding-top: 23px;
  padding-left: 15px;
}

.news-timestamp {
  overflow: hidden;
  max-height: 70px;
  max-width: 30%;
  padding-top: 20px;
  padding-left: 30px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

